import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http'

import { AppComponent } from './app.component';
import { ProductListViewComponent } from './product-list-view/product-list-view.component';
import { CartviewComponent } from './components/cartview/cartview.component';
import { ShopageComponent } from './pages/shopage/shopage.component';
import { CheckoutComponent } from './pages/shopage/checkout.component';
import { RouterModule } from '@angular/router';
import { LoginComponent} from './components/login/login.component'
import {AuthActivatorService} from './services/auth-activator.service'
import { FormGroup, FormsModule } from '@angular/forms';
import { TestComponent } from './components/test/test.component';
import { PaymentComponent } from './components/payment/payment.component';




@NgModule({
    declarations: [
        AppComponent, 
        ProductListViewComponent,
        CartviewComponent,
        ShopageComponent,
        CheckoutComponent,
        TestComponent,
        PaymentComponent
    ],
    imports: [
        BrowserModule,
        HttpClientModule,        
        FormsModule,
        
        RouterModule.forRoot([
            { path: "", component: ShopageComponent },
            { path: "checkout", component: CheckoutComponent, canActivate :[AuthActivatorService] },
            { path:"login", component : TestComponent},
            { path: "payment", component : PaymentComponent},
            { path:"**", redirectTo :"/"}
        ], {useHash:false})
        
    ],
    providers: [],
    bootstrap: [AppComponent]
    
})
export class AppModule { }

